#panelRoot {
  display: grid;
  grid-template-areas: "menu    menu" "settings settings" "content sidepane";
  grid-template-rows: auto auto 1fr;
  grid-template-columns: 1fr auto;
  height: 100vh;
}

#panelMenu {
  grid-area: menu;
  border-top: solid 0px;
}

#panelTree {
  grid-area: content;
  overflow: auto;
}

#panelSidepane {
  grid-area: sidepane;
  overflow: auto;
}

#panelSettings {
  grid-area: settings;
}

#panelSettings.hidden {
  display: none;
}