.treeItem {
  position: relative;
  cursor: pointer;
  /* Here I'm using the icon color, because these styles are related to the little triangle on the side of a context in the tree */
  /*
      Horrible, but I couldn't find any other way to do it.
      It will show the context actions (the little icons on the right) when the tree item is hovered
  */
}
.treeItem:hover {
  background-color: var(--contextHoverBg);
}
.treeItem[data-selected] {
  background-color: var(--contextSelectedBg);
}
.treeItem[data-selected] .treeItemContext {
  background-color: var(--contextSelectedBg) !important;
}
.treeItem .treeItemContext {
  cursor: pointer;
}
.treeItem .contextToggle {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(-18px);
  padding: 5px;
}
.treeItem .contextToggleIcon {
  display: inline-block;
  position: relative;
}
.treeItem[data-status=closed] .contextToggle .contextToggleIcon {
  border-left: solid 4px var(--icon);
  border-top: solid 4px transparent;
  border-bottom: solid 4px transparent;
}
.treeItem[data-status=open] .contextToggle .contextToggleIcon {
  border-top: solid 4px var(--icon);
  border-left: solid 4px transparent;
  border-right: solid 4px transparent;
  top: -2px;
}
.treeItem:hover .treeItemContext > div:nth-child(2) {
  background-color: var(--contextHoverBg);
  display: flex;
}
.treeItem[data-selected] .treeItemContext > div:nth-child(2) {
  background-color: var(--contextSelectedBg);
  display: flex;
}